body, #root, .snake-app {
  margin: 0;
  font-family: 'Inter', Arial, sans-serif;
  background: #f5f7fa;
}
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 24px 5vw;
  background: #fff;
  border-bottom: 1px solid #eee;
}
.navbar-brand {
  font-size: 22px;
  font-weight: 700;
  color: #21b657;
}
.user-name{
  font-size: 22px;
  font-weight: 700;
  color: #0e0e0e;
}
.nav-buttons button {
  margin-left: 12px;
  padding: 8px 18px;
  border-radius: 6px;
  border: none;
  font-size: 16px;
  cursor: pointer;
}
.login-btn {
  background: #e6e9ef;
  color: #222;
}
.getstarted-btn {
  background: #21b657;
  color: #fff;
  font-weight: 700;
}
.hero {
  background: #fff;
  padding: 40px 8vw 30px 8vw;
  text-align: center;
}
.hero h1 {
  color: #21b657;
  font-size: 40px;
  margin-bottom: 14px;
  font-weight: 800;
}
.hero p {
  font-size: 18px;
  margin-top: 0;
  margin-bottom: 15px;
}
.emergency-btn {
  background: #e33636;
  color: #fff;
  font-size: 20px;
  font-weight: 700;
  padding: 14px 38px;
  border-radius: 8px;
  border: none;
  margin-top: 8px;
  cursor: pointer;
}
.emergency-desc {
  color: #21b657;
  font-size: 14px;
  margin-top: 10px;
}

.chooseportal {
  padding: 36px 4vw 18px 4vw;
  text-align: center;
}
.chooseportal h2 {
  font-size: 28px;
  font-weight: 700;
  margin-bottom: 6px;
}
.chooseportal p {
  margin-bottom: 20px;
}
.portals {
  display: flex;
  gap: 32px;
  flex-wrap: wrap;
  justify-content: center;
}
.portal-card {
  background: #fff;
  border-radius: 13px;
  box-shadow: 0 2px 16px rgba(33,182,87,0.09);
  padding: 24px;
  flex: 1 0 285px;
  max-width: 320px;
  text-align: left;
  margin-bottom: 18px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.portal-icon {
  font-size: 32px;
  margin-bottom: 4px;
}
.portal-icon.green {
  color: #21b657;
}
.portal-icon.blue {
  color: #2b55a8;
}
.portal-icon.purple {
  color: #a252c2;
}
.portal-card h3 {
  color: #21b657;
  font-size: 20px;
  margin: 3px 0 6px 0;
}
.portal-card p {
  font-size: 15px;
  margin-bottom: 9px;
}
.portal-card ul {
  margin: 0 0 11px 0;
  padding-left: 22px;
  font-size: 15px;
}
.portal-btn {
  width: 100%;
  font-size: 16px;
  padding: 9px 0;
  border-radius: 6px;
  border: none;
  font-weight: 600;
  margin-top: auto;
  cursor: pointer;
}
.portal-btn.customer {
  background: #21b657;
  color: #fff;
}
.portal-btn.business {
  background: #2b55a8;
  color: #fff;
}
.portal-btn.admin {
  background: #a252c2;
  color: #fff;
}

.why {
  background: #f7faf7;
  text-align: center;
  padding: 40px 8vw 26px 8vw;
}
.why h2 {
  font-size: 26px;
  font-weight: 700;
  color: #21b657;
  margin-bottom: 16px;
}
.why-cards {
  display: flex;
  gap: 28px;
  flex-wrap: wrap;
  justify-content: center;
}
.why-card {
  background: #fff;
  padding: 28px 14px;
  border-radius: 10px;
  box-shadow: 0 2px 10px rgba(33,182,87,0.08);
  min-width: 170px;
  max-width: 220px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.why-icon.green {
  color: #21b657;
  font-size: 32px;
  margin-bottom: 7px;
}
.why-card h4 {
  font-size: 17px;
  font-weight: 650;
  margin: 0 0 8px 0;
}
.why-card p {
  font-size: 14px;
  text-align: center;
  margin-bottom: 0;
}

footer {
  background: #182c3a;
  color: #fff;
  padding: 32px 0 12px 0;
  font-size: 15px;
}
.footer-main {
  display: flex;
  justify-content: space-around;
  align-items: flex-start;
  flex-wrap: wrap;
  gap: 24px;
  padding: 0 18px 18px 18px;
}
.footer-main h3 {
  color: #21b657;
  margin: 0 0 7px 0;
}
.footer-main h4 {
  color: #6ae47d;
  font-size: 15px;
  margin-bottom: 7px;
}
.footer-main div {
  margin-bottom: 8px;
}
.footer-bottom {
  text-align: center;
  font-size: 13px;
  color: #b3c6da;
  border-top: 1px solid #2a3c54;
  padding-top: 4px;
}

@media (max-width: 1050px) {
  .navbar, .footer-main, .portals, .why-cards {
    flex-direction: column;
    align-items: center;
  }
}

@media (max-width: 700px) {
  .hero h1 {font-size: 29px;}
  .navbar-brand {font-size: 17px;}
  .chooseportal h2, .why h2 {font-size: 19px;}
  .portals {gap: 14px;}
  .why-card {min-width: 140px; max-width: 99vw; padding: 17px 6px;}
  .footer-main {font-size: 13px;}
}

@media (max-width: 480px) {
  .navbar {padding: 14px 3vw;}
  .hero {padding: 20px 2vw;}
  .chooseportal {padding: 18px 2vw;}
  .why {padding: 16px 2vw;}
  .footer-main {padding: 2px 2vw 10px 2vw;}
}
.auth-bg {
  min-height: 100vh;
  background: linear-gradient(135deg, #e9fbe6 60%, #fff 100%);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 24px 5vw;
}
.auth-card {
  background: #fff;
  border-radius: 14px;
  box-shadow: 0 2px 24px rgba(33,182,87,0.14);
  max-width: 370px;
  width: 100%;
  padding: 38px 28px 22px 28px;
  text-align: center;
}
.auth-icon {
  font-size: 38px;
  color: #21b657;
  margin-bottom: 20px;
}
.auth-title {
  font-size: 26px;
  font-weight: bold;
  color: #21b657;
  margin-bottom: 4px;
}
.auth-desc, .auth-form-desc, .auth-form-title {
  font-size: 15px;
  color: #222;
  margin-bottom: 11px;
}
.auth-form-title {
  font-weight: 600;
  margin-top: 6px;
}
.auth-input {
  width: 100%;
  font-size: 15px;
  padding: 12px 14px;
  border-radius: 7px;
  border: 1px solid #e3e6ef;
  margin-bottom: 16px;
  background: #f5f7fa;
  outline: none;
  transition: border 0.2s;
}
.auth-input:focus {
  border: 1.6px solid #21b657;
}
.auth-btn {
  background: #21b657;
  color: #fff;
  font-weight: 700;
  font-size: 16px;
  border-radius: 8px;
  padding: 13px 0;
  width: 100%;
  border: none;
  margin-top: 5px;
  cursor: pointer;
}
.auth-bottom {
  font-size: 14px;
  margin-top: 18px;
}
.auth-link {
  color: #21b657;
  text-decoration: none;
  font-weight: 600;
  margin-left: 2px;
}
.auth-home-link {
  margin-top: 20px;
  font-size: 13px;
}
@media (max-width: 700px) {
  .auth-card {
    padding: 24px 8px 15px 8px;
    max-width: 99vw;
  }
}

.emergency-rescue-container {
  max-width: 720px;
  margin: 32px auto 32px auto;
  font-family: 'Inter', Arial, sans-serif;
  background: #fff;
  padding: 0;
  border-radius: 16px;
  box-shadow: 0 6px 28px 0 rgba(38,35,68,0.11);
  min-height: 90vh;
  display: flex;
  flex-direction: column;
}

.emergency-bar {
  background: #e5483b;
  color: #fff;
  padding: 12px 16px 10px 16px;
  position: sticky;
  top: 0;
  z-index: 10;
  border-radius: 16px 16px 0 0;
  box-sizing: border-box;
  font-size: 14px;
}
.bar-icon {
  font-size: 22px;
  vertical-align: middle;
  margin-right: 7px;
}
.bar-title {
  font-weight: bold;
  font-size: 16px;
}
.bar-sub {
  font-size: 12px;
  display: block;
}
.bar-back {
  position: absolute;
  right: 18px;
  top: 14px;
  font-size: 16px;
  background: none;
  border: none;
  color: #fff;
  cursor: pointer;
  font-weight: 500;
  outline: none;
}

.emg-heading { text-align: center; margin: 22px 12px 10px 12px; }
.emg-heading h2 { color: #c60006; margin: 0 0 7px 0; font-weight: 800; font-size: 27px; letter-spacing: 0.1px; }
.emg-desc { color: #333; font-size: 16px; margin-bottom: 13px; }

.emg-form-card {
  background: #fdfdfd;
  border-radius: 12px;
  margin: 0 16px 15px 16px;
  padding: 18px 12px 10px 12px;
  box-shadow: 0 1px 8px rgba(228,72,59,0.07);
  font-size: 15px;
  display: flex;
  flex-direction: column;
}
.emg-form-title {
  font-weight: 700;
  margin-bottom: 4px;
  display: flex;
  align-items: center;
  color: #d73623;
  gap: 5px;
}
.form-icon { font-size: 19px; }

.emg-form-desc {
  font-size: 13px;
  color: #777;
  margin-bottom: 9px;
}

/* Inputs and label: spacing and focus effect */
.emg-form-card label {
  margin-bottom: 11px;
  color: #222;
  font-weight: 500;
  display: block;
}
.emg-form-card input,
.emg-form-card textarea,
.emg-form-card select {
  width: 100%;
  padding: 12px 10px;
  border-radius: 7px;
  border: 1px solid #ccd1db;
  font-size: 16px;
  margin-top: 5px;
  margin-bottom: 2px;
  background: #f6f7fb;
  transition: border 0.2s;
  box-sizing: border-box;
}
.emg-form-card input:focus,
.emg-form-card textarea:focus,
.emg-form-card select:focus {
  border: 1.2px solid #e5483b;
  outline: none;
}
.emg-form-card textarea {
  resize: vertical;
  min-height: 54px;
  max-height: 130px;
}

.emg-submit-btn {
  background: #e5483b;
  color: #fff;
  font-size: 16px;
  font-weight: 700;
  padding: 13px 0;
  border: none;
  border-radius: 8px;
  margin-top: 7px;
  margin-bottom: 2px;
  cursor: pointer;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  box-shadow: 0 2px 4px 0 rgba(228,72,59,0.09);
  transition: background 0.18s;
}
.emg-submit-btn:hover {
  background: #c60006;
}
.emg-success {
  margin-top: 13px;
  color: #168e3b;
  font-weight: 600;
  text-align: center;
  font-size: 15px;
}

.emg-instructions {
  margin: 12px 18px 24px 18px;
  flex: 1;
  font-size: 15px;
}

.inst-section {
  border-radius: 10px;
  margin-bottom: 13px;
  padding: 15px 17px;
}

.inst-yellow {
  background: #fffbec;
  border-left: 4px solid #f6c700;
}
.inst-title {
  font-weight: 800;
  color: #d73623;
  font-size: 16px;
  margin-bottom: 8px;
  letter-spacing: 0.3px;
}
.inst-yellow ul {
  padding-left: 19px;
  font-size: 15px;
  margin-bottom: 11px;
}
.inst-yellow li { margin-bottom: 8px; }
.inst-yellow .red { color: #e5483b; }
.inst-response {
  border-radius: 6px;
  border-left: 4px solid #156cd5;
  background: #f8fbff;
  color: #222;
  padding: 9px 12px;
  margin-top: 9px;
  font-size: 14px;
}

.inst-pink {
  background: #fdf1f5;
  border-left: 4px solid #f38daa;
  color: #cd3175;
  font-size: 15px;
  padding-bottom: 7px;
}
.inst-pink ul {
  padding-left: 19px;
  margin: 7px 0 2px 0;
}
.inst-pink li {
  font-size: 15px;
  margin-bottom: 3px;
}

/* Responsive Design */
@media (max-width: 700px) {
  .emergency-rescue-container {
    min-width: 100vw;
    max-width: 100vw;
    border-radius: 0;
    margin: 0;
    box-shadow: none;
    min-height: 100vh;
  }
  .emg-heading { margin: 18px 7px 8px 7px; }
  .emg-form-card, .emg-instructions {
    margin: 0 4px 0 4px;
    padding-left: 5px;
    padding-right: 5px;
  }
}
@media (max-width: 400px) {
  .bar-title { font-size: 13.5px; }
  .bar-sub { font-size: 10px; }
  .bar-icon { font-size: 15px; }
  .bar-back { font-size: 14px;}
  .emg-heading h2 { font-size: 20px; }
  .emg-desc { font-size: 12px; }
}

/* Slider */

.slider-container {
  width: 100%;
  max-width: 1080px;
  margin: 18px auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
}

.slider-image-wrap {
  width: 100%;
  overflow: hidden;
  border-radius: 16px;
  background: #f5f7fa;
  box-shadow: 0 2px 12px rgba(0,0,0,.08);
}
.slider-image {
  width: 100%;
  height: 210px;
  object-fit: cover;
  display: block;
  border-radius: 16px;
}

.slider-arrow {
  position: absolute;
  top: 50%;
  background-color: rgba(229,72,59,.92);
  color: #fff;
  border: none;
  font-size: 28px;
  width: 40px;
  height: 40px;
  cursor: pointer;
  border-radius: 50%;
  z-index: 2;
  transform: translateY(-50%);
  opacity: 0.8;
  transition: background .2s;
}
.slider-arrow.left { left: 5px; }
.slider-arrow.right { right: 5px; }
.slider-arrow:hover { opacity: 1; background: #1ac600; }

.slider-dots {
  margin-top: 8px;
  display: flex;
  gap: 6px;
}
.slider-dot {
  width: 10px;
  height: 10px;
  background: #ddd;
  border-radius: 50%;
  display: inline-block;
  cursor: pointer;
  transition: background 0.2s;
}
.slider-dot.active { background: #e5483b; }

@media (max-width: 500px) {
  .slider-image { height: 44vw; min-height: 120px; }
  .slider-arrow { width: 34px; height: 34px; font-size: 21px; }
  .slider-container { max-width: 98vw; }
}
.slider-image {
  width: 100%;
  height: 310px;
  object-fit: cover;
  display: block;
  border-radius: 16px;
  opacity: 1;
  transition: opacity 0.25s ease;
}

.slider-fade-in { opacity: 1; }
.slider-fade-out { opacity: 0; transition: opacity 0.2s ease; }

/* User Management btn */
.usr-mngt-btn{
  background-color: #1e6e3c;
  color: white;
  border: none;
  padding: 10px 14px;
  border-radius: 6px;
  font-size: 14px;
  margin-right: 10px;
  cursor: pointer;
  transition: 0.3s;
}
.usr-mngt-btn:hover {
  background-color: #17a758;
}
/* Admin Panel btn */
.admin-btn{
  background-color: #1e6e3c;
  color: white;
  border: none;
  padding: 10px 14px;
  border-radius: 6px;
  font-size: 14px;
  margin-right: 10px;
  cursor: pointer;
  transition: 0.3s;
}
.admin-btn:hover {
  background-color: #17a758;
}

/* Notification Button */
.notification-btn {
  background-color: #1e6e3c;
  color: white;
  border: none;
  padding: 8px 14px;
  border-radius: 6px;
  font-size: 14px;
  margin-right: 10px;
  cursor: pointer;
  transition: 0.3s;
}

.notification-btn:hover {
  background-color: #17a758;
}

/* Notification Page */

.notification-page {
  max-width: 900px;
  margin: 32px auto;
  padding: 0 16px;
  font-family: 'Inter', Arial, sans-serif;
}

.notification-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  flex-wrap: wrap;
  gap: 12px;
}

.notification-header h2 {
  color: #21b657;
  font-size: 28px;
  font-weight: 700;
  margin: 0;
}

.back-btn {
  background: #21b657;
  color: #fff;
  border: none;
  padding: 10px 18px;
  border-radius: 7px;
  font-size: 15px;
  font-weight: 600;
  cursor: pointer;
  transition: background 0.2s;
}

.back-btn:hover {
  background: #1d9e4d;
}

.notification-list {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.notification-card {
  background: #fff;
  border-radius: 12px;
  padding: 18px;
  box-shadow: 0 2px 12px rgba(0,0,0,0.08);
  position: relative;
  border-left: 4px solid #21b657;
}

.notification-status {
  position: absolute;
  top: 12px;
  right: 12px;
  padding: 4px 12px;
  border-radius: 12px;
  font-size: 12px;
  font-weight: 600;
  color: #fff;
}

.notification-card h3 {
  color: #e5483b;
  font-size: 18px;
  margin: 0 0 12px 0;
  font-weight: 700;
}

.notification-details {
  margin-bottom: 12px;
}

.notification-details p {
  margin: 6px 0;
  font-size: 15px;
  color: #333;
}

.notification-details strong {
  color: #555;
  font-weight: 600;
}

.notification-time {
  font-size: 13px;
  color: #777;
  font-style: italic;
}

.loading, .error, .no-notifications {
  text-align: center;
  padding: 40px 20px;
  font-size: 16px;
  color: #777;
}

.error {
  color: #e5483b;
}

.emg-error {
  background: #fdf1f5;
  border-left: 4px solid #e5483b;
  color: #e5483b;
  padding: 12px;
  border-radius: 6px;
  margin-bottom: 12px;
  font-size: 14px;
}

@media (max-width: 600px) {
  .notification-header {
    flex-direction: column;
    align-items: flex-start;
  }
  
  .notification-header h2 {
    font-size: 22px;
  }
}


.admin-panel {
  max-width: 1200px;
  margin: 0 auto;
  padding: 24px;
  font-family: 'Inter', Arial, sans-serif;
  background: #f5f7fa;
  min-height: 100vh;
}

.admin-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 32px;
  background: #fff;
  padding: 20px 24px;
  border-radius: 12px;
  box-shadow: 0 2px 12px rgba(0,0,0,0.08);
}

.admin-brand h1 {
  margin: 0;
  color: #21b657;
  font-size: 28px;
  font-weight: 800;
}

.admin-brand p {
  margin: 4px 0 0 0;
  color: #666;
  font-size: 14px;
}

.admin-back-btn {
  background: #21b657;
  color: #fff;
  border: none;
  padding: 10px 20px;
  border-radius: 8px;
  font-size: 15px;
  font-weight: 600;
  cursor: pointer;
  transition: background 0.2s;
}

.admin-back-btn:hover {
  background: #1d9e4d;
}

/* Statistics Cards */
.stats-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
  gap: 20px;
  margin-bottom: 32px;
}

.stat-card {
  background: #fff;
  padding: 24px;
  border-radius: 12px;
  box-shadow: 0 2px 12px rgba(0,0,0,0.08);
  display: flex;
  align-items: center;
  gap: 16px;
  border-left: 4px solid;
}

.stat-card.total { border-left-color: #21b657; }
.stat-card.pending { border-left-color: #f6c700; }
.stat-card.progress { border-left-color: #156cd5; }
.stat-card.completed { border-left-color: #168e3b; }

.stat-icon {
  font-size: 40px;
}

.stat-content h3 {
  margin: 0;
  font-size: 32px;
  font-weight: 800;
  color: #333;
}

.stat-content p {
  margin: 4px 0 0 0;
  color: #666;
  font-size: 14px;
}

/* Controls */
.admin-controls {
  display: flex;
  gap: 16px;
  margin-bottom: 24px;
  flex-wrap: wrap;
}

.filter-buttons {
  display: flex;
  gap: 8px;
  flex-wrap: wrap;
}

.filter-btn {
  padding: 10px 18px;
  border: 2px solid #ddd;
  background: #fff;
  border-radius: 8px;
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s;
}

.filter-btn.active {
  background: #21b657;
  color: #fff;
  border-color: #21b657;
}

.filter-btn:hover {
  border-color: #21b657;
}

.search-input {
  flex: 1;
  min-width: 250px;
  padding: 10px 16px;
  border: 2px solid #ddd;
  border-radius: 8px;
  font-size: 15px;
  transition: border-color 0.2s;
}

.search-input:focus {
  outline: none;
  border-color: #21b657;
}

/* Requests Section */
.requests-section {
  background: #fff;
  padding: 24px;
  border-radius: 12px;
  box-shadow: 0 2px 12px rgba(0,0,0,0.08);
}

.requests-section h2 {
  margin: 0 0 20px 0;
  color: #333;
  font-size: 22px;
  font-weight: 700;
}

.requests-table {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.request-card {
  border: 1px solid #e5e5e5;
  border-radius: 10px;
  padding: 18px;
  background: #fafafa;
  transition: box-shadow 0.2s;
}

.request-card:hover {
  box-shadow: 0 4px 16px rgba(0,0,0,0.1);
}

.request-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 16px;
  gap: 12px;
}

.request-header h3 {
  margin: 0;
  color: #21b657;
  font-size: 18px;
  font-weight: 700;
}

.request-time {
  font-size: 13px;
  color: #777;
  display: block;
  margin-top: 4px;
}

.status-select {
  padding: 8px 12px;
  border-radius: 6px;
  border: 2px solid;
  font-weight: 600;
  font-size: 14px;
  cursor: pointer;
  transition: all 0.2s;
}

.status-select.pending {
  background: #fffbec;
  border-color: #f6c700;
  color: #b8860b;
}

.status-select.in-progress {
  background: #f0f7ff;
  border-color: #156cd5;
  color: #156cd5;
}

.status-select.completed {
  background: #f0fdf4;
  border-color: #168e3b;
  color: #168e3b;
}

.request-body {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 12px;
  margin-bottom: 16px;
}

.request-detail {
  font-size: 15px;
}

.request-detail strong {
  color: #555;
  display: block;
  margin-bottom: 4px;
}

.request-detail.full-width {
  grid-column: 1 / -1;
}

.urgency-badge {
  display: inline-block;
  padding: 4px 10px;
  border-radius: 12px;
  font-size: 13px;
  font-weight: 600;
  margin-left: 8px;
}

.urgency-badge.high {
  background: #fdf1f5;
  color: #e5483b;
}

.urgency-badge.medium {
  background: #fff7e6;
  color: #f6c700;
}

.urgency-badge.low {
  background: #f0f7ff;
  color: #156cd5;
}

.request-actions {
  display: flex;
  gap: 8px;
  flex-wrap: wrap;
}

.action-btn {
  padding: 8px 14px;
  border: none;
  border-radius: 6px;
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s;
}

.action-btn.view {
  background: #156cd5;
  color: #fff;
}

.action-btn.call {
  background: #21b657;
  color: #fff;
}

.action-btn.delete {
  background: #e5483b;
  color: #fff;
}

.action-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 8px rgba(0,0,0,0.15);
}

.loading, .no-requests {
  text-align: center;
  padding: 40px 20px;
  color: #777;
  font-size: 16px;
}

/* Responsive */
@media (max-width: 768px) {
  .admin-header {
    flex-direction: column;
    align-items: flex-start;
    gap: 12px;
  }

  .stats-container {
    grid-template-columns: 1fr;
  }

  .admin-controls {
    flex-direction: column;
  }

  .search-input {
    width: 100%;
  }

  .request-header {
    flex-direction: column;
  }

  .request-body {
    grid-template-columns: 1fr;
  }
}

.user-mgmt-root { max-width: 900px; margin: 40px auto; font-family: Inter, Arial, sans-serif;}
h2 {color: #21b657;}

.user-form { display: flex; gap: 12px; margin-bottom: 22px; flex-wrap: wrap;}
.user-form input, .user-form select {
  padding: 8px; font-size: 15px; border-radius: 7px; border: 1px solid #ddd; min-width: 170px;
}
.user-form button { padding: 8px 14px; font-size: 14px; border-radius: 7px; margin-left: 10px; font-weight: 600; background: #21b657; color: #fff; border: none;}
.user-form button[type="button"]{background:#e5483b;}
.user-error { color: #e5483b; margin-left: 18px; }

.user-search-box { margin-bottom: 16px;}
.user-search-box input { padding: 8px; border-radius: 8px; border: 1px solid #ddd; width: 320px; max-width: 100%;}

.user-table {width: 100%; border-collapse: collapse;}
.user-table th, .user-table td { padding: 9px; }
.user-table th { background: #ededed; text-align: left; }
.user-table tr:nth-child(even) { background: #fafafa; }
.edit-btn, .delete-btn {
  padding: 6px 13px; border-radius: 7px; border: none; font-weight: 600; font-size: 13px;
  margin-right: 7px; cursor: pointer;
}
.edit-btn { background: #156cd5; color: #fff;}
.delete-btn { background: #e5483b; color: #fff;}
.edit-btn:hover { background:#134c99;}
.delete-btn:hover { background:#b82f1c;}
@media (max-width:600px){
  .user-form{flex-direction:column;}
  .user-table th, .user-table td{padding:7px;}
}
